import { toast } from 'react-toastify'
import removeHyphen from './removeHyphen.js'

export default function isValid(name, tel) {
  // 
  if (name === '' || tel === '') {
    toast.error('')
    return false
  }

  // 
  const regex = new RegExp(/^\d+$/)
  if (!regex.test(removeHyphen(tel))) {
    toast.error('')
    return false
  }

  // 1011
  if (tel.length < 10 || tel.length > 11) {
    toast.error('1011')
    return false
  }

  return true
}
